@import url(https://fonts.googleapis.com/css?family=Varela+Round);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    padding: 50px 20px;
    background: linear-gradient(135deg, #43cdef 0%, #1f9ed3 75%);
    color: #fff;
    text-align: center;
}

.header h1 {
    font-size: 50px;
    font-weight: normal;
}

.header h2 {
    font-size: 20px;
    font-weight: normal;
}

.search-bar {
    margin: auto;
    padding: 20px;
    max-width: 800px;
    display: flex;
}

.search-bar input {
    border: 1px solid #5bc0de;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    padding: 10px;
    flex: 1 1;
}

.search-bar button {
    border: 1px solid #5bc0de;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    padding: 10px;
    background-color: #5bc0de;
    color: #fff;
    cursor: pointer;
}

.tile {
	margin: 10px;
	border-radius: 5px;
	padding: 10px;
	width: 200px;
    height: 200px;
	text-align: center;
	cursor: pointer;
	-webkit-transition: 0.25s;
	transition: 0.25s;
}

.tile:hover {
    background-color: #f7f7f7;
}
.tile-grid {
    padding: 50px 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
}

h4 {
    font-weight: normal;
}
hr {
    margin: 20px 0;
    border-top: 1px solid #eee;
}

.modal {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: none;
}

.modal.visible {
    display: block;
}

.modal-overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10000;
    background-color: #333;
    opacity: 0.9;
}

.modal-dialog {
    border-radius: 5px;
    width: 400px;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 10010;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: #fff;
    text-align: center;
}

.modal-header {
    border-radius: 5px 5px 0 0;
    padding: 20px;
    position: relative;
    background: linear-gradient(135deg, #43cdef 0%, #1f9ed3 75%);
    color: #fff;
}

.modal-close {
    padding: 10px;
    position: absolute;
    top: 50%;
    right: 10px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background: none;
    color: #fff;
    font-size: 20px;
    cursor: pointer;
}

.modal-body {
    padding: 20px;
}

.modal-body h4 {
    font-weight: bold;
}

.footer {
    padding: 40px 20px;
    background-color: #f7f7f7;
    color: #777;
    text-align: center;
}

* {
    margin: 0;
    border: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Varela Round", sans-serif;
}

