.search-bar {
    margin: auto;
    padding: 20px;
    max-width: 800px;
    display: flex;
}

.search-bar input {
    border: 1px solid #5bc0de;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    padding: 10px;
    flex: 1;
}

.search-bar button {
    border: 1px solid #5bc0de;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    padding: 10px;
    background-color: #5bc0de;
    color: #fff;
    cursor: pointer;
}
