hr {
    margin: 20px 0;
    border-top: 1px solid #eee;
}

.modal {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: none;
}

.modal.visible {
    display: block;
}

.modal-overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10000;
    background-color: #333;
    opacity: 0.9;
}

.modal-dialog {
    border-radius: 5px;
    width: 400px;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 10010;
    transform: translate(-50%, -50%);
    background-color: #fff;
    text-align: center;
}

.modal-header {
    border-radius: 5px 5px 0 0;
    padding: 20px;
    position: relative;
    background: linear-gradient(135deg, #43cdef 0%, #1f9ed3 75%);
    color: #fff;
}

.modal-close {
    padding: 10px;
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    background: none;
    color: #fff;
    font-size: 20px;
    cursor: pointer;
}

.modal-body {
    padding: 20px;
}

.modal-body h4 {
    font-weight: bold;
}
